import express from 'express'
import https from 'https'
import fs from 'fs'

const app = express()
const port = 3000

app.get('/', (req, res) => res.send('Hello World!'))

let key = fs.readFileSync('./key.pem')
console.log(key)

https.createServer({
  key,
  cert: fs.readFileSync('./cert.pem'),
  passphrase: 'testkey'
}, app)
  .listen(port);
